import React from 'react';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';

import './modal.scss';

const Modal = ({ success, error, modalShow, title, message, messageError, page }) => {
  const onHide = () => {
    modalShow(false, false);
  };

  if (success) {
    return (
      <div
        className="modal fade show"
        id="myModal"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="exampleModalCenterTitle"
        aria-hidden="true"
      >
        <div className="modal-dialog modal-dialog-centered" role="document">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="exampleModalLongTitle">
                {title}
              </h5>
              <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true" onClick={onHide}>
                  &times;
                </span>
              </button>
            </div>
            <div className="modal-body">{message}</div>
            <div className="modal-footer">
              <button
                type="button"
                className="btn btn-secondary"
                data-dismiss="modal"
                onClick={onHide}
              >
                Fermer
              </button>
              <Link to={`/${page}`} path={`/${page}`}>
                <button type="button" className="btn btn-primary" onClick={onHide}>
                  Aller  la page {page}
                </button>
              </Link>
            </div>
          </div>
        </div>
      </div>
    );
  } else if (error) {
    return (
      <div
        className="modal fade show"
        id="myModal"
        tabIndex="-1"
        role="dialog"
        aria-labelledby="exampleModalCenterTitle"
        aria-hidden="true"
      >
        <div className="modal-dialog modal-dialog-centered" role="document">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="exampleModalLongTitle">
                {title}
              </h5>
              <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true" onClick={onHide}>
                  &times;
                </span>
              </button>
            </div>
            <div className="modal-body alert-danger">{messageError}</div>
            <div className="modal-footer">
              <button
                type="button"
                className="btn btn-secondary"
                data-dismiss="modal"
                onClick={onHide}
              >
                Fermer
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  } else {
    return '';
  }
};

Modal.propTypes = {
  success: PropTypes.bool.isRequired,
  error: PropTypes.bool.isRequired,
  modalShow: PropTypes.func.isRequired,
  title: PropTypes.string.isRequired,
  message: PropTypes.string.isRequired,
  messageError: PropTypes.string.isRequired,
  page: PropTypes.string.isRequired,
};

export default Modal;
